.form {

  &__input {
    width: 100%;
    height: 48px;
    @include fontParams (14px, 20px, 400);
    padding: 14px 16px;
    border-radius: 15px;
    color: black;
    border: 1px solid black;
    margin-bottom: 16px;
  }

  &__textarea {
    display: flex;
    @include fontParams (14px, 20px, 400);
    width: 100%;
    height: 148px;
    color: #000;
    padding: 14px 16px;
    border-radius: 16px;
    margin-bottom: 32px;
    resize: none;

  }

  &__submit-btn {
    width: 100%;
    height: 56px;
    border-radius: 15px;
  }
}
